<template>
  <div class="indexMenu">
      <div
        class="menu-button"
        v-for="(item,index) in menuList"
        :key="index"
        :style="{border: (item.click ? '1px solid rgba(30,207,255,0.5)' : '1px solid rgba(255,255,255,0.5)' )}"
      > 
        <div 
            class="menu-content" 
            @click="menuClick(item)"
            :style="[
                {background: (item.click ? 'rgba(44,101,171,0.6)' : 'rgba(37,64,95,,0.6)' )},
                {border: (item.click ? '2px solid rgba(30,207,255,1.0)' : '2px solid rgba(255,255,255,1.0)' )}
            ]" 
        >
            <Poptip 
                placement="left" 
            >
                {{ item.name}}
                <div 
                    class="poptip-contet"
                    slot="content"
                    v-if="item.component"
                >
                    <component :is="item.component"></component>
                </div>
            </Poptip>
        </div>
      
      </div>
  </div>
</template>

<script>
import layerPop from './layerPop.vue'; 
import roamPop from './roamPop.vue';
import carPop from './carPop.vue';
import timePop from './timePop.vue';
import weatherPop from './weatherPop.vue';
export default {
    name: 'indexMenu',
    components:{
        layerPop, roamPop, carPop, timePop, weatherPop
    },
    data(){
        return{
            menuList:[
                {
                    name: '图层',
                    key: 'layer',
                    click: false,
                    component: 'layerPop',
                },
                {
                    name: '漫游',
                    key: 'roam',
                    click: false,
                    component: 'roamPop',
                },
                {
                    name: '车流',
                    key: 'car',
                    click: false,
                    component: 'carPop',
                },
                {
                    name: 'VR',
                    key: 'vr',
                    click: false,
                },
                {
                    name: '卷帘',
                    key: 'roller',
                    click: false,
                },
                {
                    name: '时间',
                    key: 'time',
                    click: false,
                    component: 'timePop',
                },
                {
                    name: '天气',
                    key: 'weather',
                    click: false,
                    component: 'weatherPop',
                },
                {
                    name: '复位',
                    key: 'reset',
                    click: false,
                }
            ],
            
            
        }
    },
    methods:{
        menuClick(item){
            this.hideAllClickStyle();
            item.click = true;
        },
        hideAllClickStyle(){
            this.menuList.forEach( obj =>{
                obj.click = false;
            })
        }
    }
}
</script>

<style lang="less" scoped> 
.indexMenu{
    position: absolute;
    z-index: 1;
    right: 420px;
    top: 110px;
}
.menu-button{
    width: 53px;
    height: 53px;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
    border: 1px solid rgba(255,255,255,0.5);
    border-radius: 50%;
    margin-bottom: 15px; 
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;    
}
.menu-content{
    width: 46px;
    height: 46px;
    border: 2px solid #fff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(37,64,95, 0.6);
}
/deep/ .ivu-poptip-popper{
    min-width: 100px;
}
/deep/ .ivu-poptip-body{
    padding: 0;

}
/deep/ .ivu-poptip-popper[x-placement^=left] .ivu-poptip-arrow:after{
    border-left-color:  rgba(18, 118, 184, 0.3) ;
}
.poptip-contet{
    height: 46px;
    background: linear-gradient(270deg, rgba(18, 118, 184, 0.6) 0%, #1851A8 100%);
    border: 1px solid #5BE3FB;
    border-radius: 4px;
    padding: 5px;

    display: flex;
    justify-content: center;
}
</style>