<template>
  <div class="indexFooter">
      <div 
        v-for="(item,index) in sectionList"
        :key="index"
        :class="sectionClass(item)"
        :style="[{top:item.top + 'px'}, {left: (item.click ? (item.left - 35 + 'px') : (item.left + 'px')) }]"
        @click="clickSection(item)"
      >
        <div class="section-title">  {{item.name}} </div>  
      </div>
  </div>
</template>

<script>
export default {
    name: 'index-footer',
    data(){
        return{
            sectionList: CONFIG.sectionList
        }
    },
    computed:{
        sectionClass:function(){
            return function(item){
                if(item.click)
                    return 'section-one section-one-sel';
                else
                    return 'section-one';
            }
        }
    },
    mounted(){  
    },
    methods:{
        clickSection(item){
            this.sectionList.forEach(element => {
                element.click = false;
            });
            item.click = true;
        }
    }
}
</script>

<style lang="less" scoped>
.indexFooter{
    width: 100%;
    height: 105px;
    margin-left: 293px;
    position: absolute;
    z-index: 1;
    bottom: 0;
    background: url('../../../assets/img/background/dibubiaoduan.png') no-repeat;
}
.section-one{
    position: absolute;
    z-index: 2;
    height: 64px;
    width: 64px;
    background: url('../../../assets/img/background/biaoduan.png');
    display: flex;
    align-items: center;
    justify-content: center;
    color: #A3AEC0;
}
.section-one-sel{
    width: 132px;
    height: 64px;
    color: #58E3FB;
    background: url('../../../assets/img/background/biaoduan_sel.png');
}

.section-title{
    height: 36px;
    font-size: 16px;
    font-family: MicrosoftYaHei-Bold, MicrosoftYaHei;
    font-weight: bold;
    line-height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
}


</style>